/**
 * Radius class
 * Represents a radius for corners
 */
class Radius {
    constructor(x, y) {
        this.x = x;
        this.y = y || x;
    }

    static circular(radius) {
        return new Radius(radius);
    }

    static elliptical(x, y) {
        return new Radius(x, y);
    }

    static get zero() {
        return new Radius(0);
    }
}

/**
 * BorderRadius class
 * Represents border radii for all four corners
 */
class BorderRadius {
    constructor({
        topLeft = Radius.zero,
        topRight = Radius.zero,
        bottomLeft = Radius.zero,
        bottomRight = Radius.zero
    } = {}) {
        this.topLeft = topLeft;
        this.topRight = topRight;
        this.bottomLeft = bottomLeft;
        this.bottomRight = bottomRight;
    }

    /**
     * Create uniform radius
     */
    static all(radius) {
        // If radius is a number, convert to Radius.circular
        const r = typeof radius === 'number' ? Radius.circular(radius) : radius;
        return new BorderRadius({
            topLeft: r,
            topRight: r,
            bottomLeft: r,
            bottomRight: r
        });
    }

    /**
     * Create circular radius for all corners
     */
    static circular(radius) {
        return BorderRadius.all(Radius.circular(radius));
    }

    /**
     * Create for specific corners
     */
    static only({
        topLeft = Radius.zero,
        topRight = Radius.zero,
        bottomLeft = Radius.zero,
        bottomRight = Radius.zero
    } = {}) {
        return new BorderRadius({ topLeft, topRight, bottomLeft, bottomRight });
    }

    /**
     * Create horizontal
     */
    static horizontal({ left = Radius.zero, right = Radius.zero } = {}) {
        return new BorderRadius({
            topLeft: left,
            topRight: right,
            bottomLeft: left,
            bottomRight: right
        });
    }

    /**
     * Create vertical
     */
    static vertical({ top = Radius.zero, bottom = Radius.zero } = {}) {
        return new BorderRadius({
            topLeft: top,
            topRight: top,
            bottomLeft: bottom,
            bottomRight: bottom
        });
    }

    toCSS() {
        // Helper to get value
        const val = (r) => `${r.x}px`;

        return `${val(this.topLeft)} ${val(this.topRight)} ${val(this.bottomRight)} ${val(this.bottomLeft)}`;
    }
}

export {  BorderRadius };
